import React, { Component } from 'react';
import Board from './Board.component';
import swal from 'sweetalert2';
//import { Redirect } from 'react-router';

class Game extends Component {
    constructor(props) {
        super(props);
        this.state = {
            history: [
                {
                    squares: Array(9).fill(null),
                },
            ],
            stepNumber: 0,
            xIsNext: true,
        };
    }

    handleClick(i) {
        const history = this.state.history.slice(0, this.state.stepNumber + 1);
        const current = history[history.length - 1];
        const squares = current.squares.slice();
        if (calculateWinner(squares) || squares[i]) {
            return;
        }
        squares[i] = this.state.xIsNext ? 'X' : 'O';
        this.setState({
            history: history.concat([
                {
                    squares: squares,
                },
            ]),
            xIsNext: !this.state.xIsNext,
            stepNumber: history.length,
        });
    }

    jumpTo(step) {
        this.setState({
            stepNumber: step,
            xIsNext: step % 2 === 0,
        });
    }

    render() {
        const $this = this;
        const history = this.state.history;
        const current = history[this.state.stepNumber];
        const winner = calculateWinner(current.squares);
        const moves = history.map((step, move) => {
            const desc = move ? 'Go to move #' + move : 'Go to game start';
            return (
                <li key={move}>
                    <a onClick={() => this.jumpTo(move)}>{desc}</a>
                </li>
            );
        });
        let status;
        if (winner) {
            status = 'Winner: ' + winner;
            swal({
                title: 'Winner: ' + winner + '! Congratulations.',
                text: 'Do you want to play another round?',
                type: 'success',
                showCancelButton: true,
                confirmButtonText: 'Start a new game.',
                cancelButtonText: 'Stop playing.',
            }).then((result) => {
                if (result.value) {
                    setTimeout(function() {
                        window.location.reload();
                    }, 600);
                } else {
                    setTimeout(function() {
                        $this.props.history.push('/');
                    }, 600);
                }
            });
        } else {
            status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
        }

        return (
            <div className="row">
                <div className="game col-sm-3">
                    <div className="game-board">
                        <Board
                            squares={current.squares}
                            onClick={(i) => this.handleClick(i)}
                        />
                    </div>
                </div>
                <div className="col-sm-3">{status}</div>
                <div className="game-info col-sm-5">
                    <label>Game History</label>
                    <ol className="gameHistory">{moves}</ol>
                </div>
            </div>
        );
    }
}

function calculateWinner(squares) {
    const lines = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6],
    ];
    for (let i = 0; i < lines.length; i++) {
        const [a, b, c] = lines[i];
        if (
            squares[a] &&
            squares[a] === squares[b] &&
            squares[a] === squares[c]
        ) {
            return squares[a];
        }
    }
    return null;
}

export default Game;



